.pricing-top{
    text-align: center;
}
.pricing-top h2{
    font-size: 2.5rem;
    margin-bottom: 20px;
}
.pricing-top p{
    color: var(--heading-color);
    font-size: 1.1rem;
    line-height: 28px;
    margin-bottom: 50px;
    
}
.pricing-wrapper{
    display: flex;
    align-items: center;
    column-gap: 2rem;
    justify-content:space-between;
}
.pricing_item:hover{
    transform: translateY(-10px);
    box-shadow: 0px 0px 20px rgba(0,0,0,0.2);
}
.pricing_item{
    width: 33%;
    box-shadow: rgba(0,0,0,0.15)0px 5px 15px 0px;
    background: #ffff;
    border-radius: 5px;
}
.pricing_card-top{
    padding: 30px;
    border-bottom: 1px solid #d3cef2;
    text-align: center;
}


.pricing_card-top h2 {
    font-size: 1.5rem;
    margin-bottom: 0.9rem;
}

.pricing_item .services {
    padding: 30px;
}

.pricing_card-top  span {
    font-size: 1rem;
    color: #666;
}
.pricing_item .services ul li {
    display: flex;
    align-items: center;
    column-gap: 1.5rem;
    color: #666;
    line-height: 28px;
}
.pricing_item .services ul li span i {
    font-size: 0.7rem;
}
.services {
    text-align: center;
}
.services button {
    margin-top: 50px;
}




.pricing_item-02 .pricing_card-top {
    background: var(--primary-color);
    border-radius: 5px 5px 0px 0px;
}
.pricing_item-02 .pricing_card-top h2 {
    color: #fff;
    font-weight: 500;
}
.pricing_item-02 .pricing_card-top h2 span {
    color: rgba(255, .255, .255, .0.788);
}
.pricing_item-02 .services ul li span i {
    color: var(--primary-color);
}